body{
    direction: ltr;
    font-family: 'Open Sans', sans-serif;
    &.overflow{
        overflow: hidden;
    }
    background-color: #242424;
}
//loading screen
.loading{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #242424;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999;
    .loder{
        position: relative;
        width: 300px;
        height: 300px;
        transform-style: preserve-3d;
        transform: perspective(500px) rotateX(60deg);
        
        span{
            transform: translateZ(-100px);
            animation: loding 3s ease-in-out infinite;
          position: absolute;
          transform: translateZ(-100px);
          display: block;
          border: 2px solid #fff;
          box-sizing: border-box;
          border-radius: 50%;
          &:nth-child(1){
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            animation-delay: 1.4s;

        }
        &:nth-child(2){
            top: 10px;
            bottom: 10px;
            left: 10px;
            right: 10px;
            animation-delay: 1.3s;

        }
        &:nth-child(3){
            top: 20px;
            bottom: 20px;
            left: 20px;
            right: 20px;
            animation-delay: 1.2s;

        }
        &:nth-child(4){
            top: 30px;
            bottom: 30px;
            left: 30px;
            right: 30px;
            animation-delay: 1.1s;

        }
        &:nth-child(5){
            top: 40px;
            bottom: 40px;
            left: 40px;
            right: 40px;
            animation-delay: 1s;

        }
        &:nth-child(6){
            top: 50px;
            bottom: 50px;
            left: 50px;
            right: 50px;
            animation-delay: 0.9s;

        }
        &:nth-child(7){
            top: 60px;
            bottom: 60px;
            left: 60px;
            right: 60px;
            animation-delay: 0.8s;

        }
        &:nth-child(8){
            top: 70px;
            bottom: 70px;
            left: 70px;
            right: 70px;
            animation-delay: 0.7s;

        }
        &:nth-child(9){
            top: 80px;
            bottom: 80px;
            left: 80px;
            right: 80px;
            animation-delay: 0.6s;

        }
        &:nth-child(10){
            top: 90px;
            bottom: 90px;
            left: 90px;
            right: 90px;
            animation-delay: 0.5s;

        }
        &:nth-child(11){
            top: 100px;
            bottom: 100px;
            left: 100px;
            right: 100px;
            animation-delay: 0.4s;

        }
        &:nth-child(12){
            top: 110px;
            bottom: 110px;
            left: 110px;
            right: 110px;
            animation-delay: 0.3s;

        }
        &:nth-child(13){
            top: 120px;
            bottom: 120px;
            left: 120px;
            right: 120px;
            animation-delay: 0.2s;

        }
        &:nth-child(14){
            top: 130px;
            bottom: 130px;
            left: 130px;
            right: 130px;
            animation-delay: 0.1s;

        }
        &:nth-child(15){
            top: 140px;
            bottom: 140px;
            left: 140px;
            right: 140px;
            animation-delay: 0s;
        }
      }
    }
}
@keyframes loding {
    0%{
        transform: translateZ(-100px);
    }
    50%{
        transform: translateZ(100px);

    }
    100%{
        transform: translateZ(-100px);
    }
    
}
/*navbar*/
.close {
    float: right;
    font-size: 20px;
    font-weight: 700;
    line-height: unset;
    text-shadow: unset;
    opacity: .7;
}
.search-overlay{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-13%,-93%) scale(.1);
    background-color: rgba(41, 41, 41, 0.95);
    width: 100%;
    height: 100%;
    z-index: 9000;
    opacity: 0;
    visibility: hidden;
    transition: .5s;
    &.active{
        transform: translate(-50%,-50%) scale(1);
        opacity: 1;
        visibility: visible;
    
    }
    .close{
        position: absolute;
        top: 20%;
        left: 50%;
        transform: translate(-50%,-50%);

        font-size: 35px;
        color: #FFF;
        transition: .5s;
        &:hover{
            color: #9ec050
        }
    }
    .form{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: max-content;
        position: relative;
        input{
            background-color: transparent;
            border: none;
            border-bottom:2px solid rgba(255, 255, 255, 0.767);
            padding: 10px 20px;
            width: 650px;
            outline: none;
            color: #fff;
            transition: .5s;
            &::placeholder{
                color: rgba(255, 255, 255, 0.767);
            }
            &:focus{
                border-bottom:2px solid  #9ec050;
            }
            @media (max-width:425px) {
                width: 350px;
            }
            @media (max-width:375px ) {
                width: 250px;
            }

        }
        button{
            background-color: transparent;
            border: none;
            color:  rgba(255, 255, 255, 0.767);
            cursor: pointer;
            position: absolute;
            font-size: 20px;
            top: 7px;
            right: 8px;
            transition: .5s;
            outline: none;
            &:hover{
                color: #9ec050;
            }    

        }
    }
}

.side-nav{
    position: absolute;
    top: 50%;
    left: 50%;
    background-color: rgba(41, 41, 41, 0.95);
    width: 100%;
    height: 100%;
    z-index: 9000;
    transition: .5s;
    opacity:0;
    visibility: hidden;
    transform: translate(-6%, -92%) scale(0.1);

    &.nav-active{
        transform: translate(-50%,-50%) scale(1);
        opacity: 1;
        visibility: visible;
    
    }
    .top{
        position: absolute;
        left: 0;
        top: 10%;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        padding: 0 10%;
        .close{
            font-size: 35px;
            color: #FFF;
            transition: .5s;

            &:hover{
                color: #9ec050
            }
        }    
        .lang{
            margin-left: auto;
            a{
                font-size: 16px;
                color: #fff;
                font-family: "Arial";
                font-weight: bold;
            }
            transition: .5s;
            border: 1px solid rgb(52, 52, 52);
            width: 40px;
            height: 40px;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 12px;
            color: rgb(255, 255, 255);
            cursor: pointer;
            &:hover{
                background-color: #9ec050;
            }
        }
    }
    .mynav-link{
        position: absolute;
        top: 33%;
        left: 50%;
        transform: translate(-50%,-50%);
        text-align: center;
        margin-top: auto;
        height: 100px;
        @media (max-width:320px) {
            top: 38%;
        }

        &>li{
            margin-bottom: 10px;
            & > a{
                font-size: 25px;
                @media (max-width:425px) {
                    font-size: 23px;
                }
                @media (max-width:320px) {
                    font-size: 17px;
                }

                color: rgb(255, 255, 255);
                text-transform: uppercase;
                text-decoration: none;
                transition: .5s;
                backface-visibility: hidden;
                &:hover{
                    color: #9ec050;
                }
            }
        }
        .drop{
            display: block;
            align-items: center;
            ul{
                padding-inline-start: 0;
                backface-visibility: hidden;
                li{
                    padding: 5px 0; 
                    @media (max-width:320px) {
                        padding: 0 0; 
                    }
                    a{
                        width: 100%;
                        font-size: 13px;
                        @media (max-width:320px) {
                            font-size: 12px;
                            width: 100%;
                        }
                        color:rgba(255, 255, 255, 0.767);
                        text-decoration: none;
                        &:hover{
                            color: #9ec050;
                        }    
                    }
                }
            }
        }
    }
}

.big-nav{
    .main-nav{
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        padding: 24px 0 10px;

        .logo-box{
            position: absolute;
            left: -1%;
            top: 0;
            z-index: 1111;

