                    width: 70%;

                }
                @media (max-width:375px) {
                    width: 60%;

                }
                
            }
        }
        .nav-link{
            display: flex;
            position: relative;
            margin-left: auto;
            margin-right: 30px;
            @media (max-width:991px) {
                display: none;
            }
        
            & > li{
                margin-right: 30px;
                @media (max-width:1024px) {
                    margin-right: 20px;
                }

                & > a{
                    font-size: 14px;
                    color: rgb(255, 255, 255);
                    text-transform: uppercase;
                    text-decoration: none;
                    transition: .5s;
                    &:hover{
                        color: #9ec050;
                    }
                }
            }
            .drop{
                .sup-link{
                    z-index: 1111;
                    position: absolute;
                    top: 42px;
                    left: 160px;
                    background-color: rgba(41, 41, 41, 0.902);
                    width: 279px;
                    height: 175px;
                    padding: 10px 17px;
                    margin: 0;
                    transform: scale(.5) translateY(-100px);
                    transition: .5s;
                    opacity: 0;
                    visibility: hidden;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    li{
                        padding: 7px 0; 
                        &:not(:last-child){
                            border-bottom: 1px solid rgb(55, 55, 55);
                        }
                        a{
                            font-size: 13px;
                            color: rgb(119, 118, 118);
                            transition: .5s;
                            text-decoration: none;
                            &:hover{
                                color: #9ec050;
                            }    
                        }
                    }  
                }
                &:hover{
                    .sup-link{
                        transform: scale(1) translateY(0);
                        opacity:1;
                        visibility: visible;
                    }
                }
            }
        }
        .nav-icon{
        
            display: flex;
            justify-content: center;
            align-items: center;
            @media (max-width:991px) {
                display: none;
            }

            li{
                margin-right: 23px;
                @media (max-width:1024px) {
                    margin-right: 15px;
                }
                a{
                    font-size: 16px;
                    color: rgb(88, 88, 88);
                    transition: .5s;
                    &:hover{
                        color: #fff;
                    }
                }
                &.search-icon{
                    margin-left: 18px;
                    margin-right: 8px;
                }
                &.search-icon,
                &.lang a{
                    font-size: 16px;
                    color: #fff;
                    font-family: "Arial";
                    font-weight: bold;
                    transition: .5s;
                }
                &.search-icon,
                &.lang ,
                &.sm-nav{
                    border: 1px solid rgb(52, 52, 52);
                    width: 40px;
                    height: 40px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-size: 12px;
                    color: rgb(255, 255, 255);
                    cursor: pointer;
                    transition: .5s;
                    &:hover{
                        background-color: #9ec050;
                    }    
                }
                &.lang{
                    margin-right: 45px;
                    @media (min-width:1920px) {
                        margin-right: 63px;
                    }
                    @media (max-width:991px) {
                        margin-right: 8px;
                        display: none;
                        
                    }
        
                }
            }
        }
        .sm-icon{
            display: flex;
            justify-content: center;
            align-items: center;
            margin-left: auto;
            display: none;
            @media (max-width:991px) {
                display: flex;
            }

            li{
                &.search-icon{
                    margin-right: 15px;
                }
                &.search-icon,
                &.sm-nav{
                    transition: .5s;
                    
                    border: 1px solid rgb(52, 52, 52);
                    width: 40px;
                    height: 40px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-size: 16px;
                    color: rgb(255, 255, 255);
                    cursor: pointer;
                    &:hover{
                        background-color: #9ec050;
                        
                    }    

                }
            }

        }
    }
}
.main-header{
    overflow: hidden;
    .con{
        width: 93%;
        margin: auto;
        position: relative;
        @media (max-width:991px) {
            width: 100%;
        }
    
        .caption{
            position: absolute;
            top: 50%;
            left: 26%;
            transform: translate(-50% , -50%);
            @media (max-width:1024px) {
                left: 30%;
            }
            @media (max-width:991px) {
                left: 29%;
            }
            @media (max-width:768px) {
                left: 35%;

            }
            @media (max-width:480px) {
                text-align: center;
                left: 50%;
                top: 55%;
                width: 80%;
            }
            @media (min-width:1920px) {
                left: 21%;

            }

            h2{
                font-size: 48px;
                color: rgb(255, 255, 255);
                font-weight: bold;
                @media (max-width:768px) {
                    font-size: 38px;

                }
                @media (max-width:480px) {
                    font-size: 25px;

                }
                @media (max-width:375px) {
                    font-size: 23px;

                }
                @media (max-width:320px) {
                    font-size: 19px;

                }
            }
            p{
                font-size: 20px;
                color: rgb(255, 255, 255); 
                @media (max-width:768px) {
                    font-size: 15px;

                }
                @media (max-width:480px) {
                    font-size: 14px;

                }
                @media (max-width:320px) {
                    font-size: 12px;

                }
                 
            }
            span{
                display: block;
                @media(max-width:480px){
                    display: unset;
                }
    
            }
        }
        .owl-dots{
            position: absolute;
            top: 65%;
            left: 11%;
            transform: translateX(-50%);
            @media (max-width:1024px) {
                top: 66%;
            }
            @media (max-width:768px) {
                left: 18.5%;

            }
            @media (min-width:1920px) {
                top: 60%;

            }

            @media(max-width:480px){
                display: none;
            }
        .owl-dot{
                background-color: rgba(255, 255, 255, 0.302);
                width: 32px;
                height: 2px;
                display: inline-block;
                outline: none;
                transition: .5s;
                &:not(:first-child){
                    margin-right: 8px;
                    @media (max-width:375px) {
                        margin-right: 5px;
    
                    }
                }
                &.active{
                    background-color:#9ec050;
                }
            }
        }
        .owl-nav{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 0;
            width: 100%;
            margin-top: 0;
            height: 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0 30px;
            @media (max-width:991px) {
                display: none;
            }
            .owl-prev,
            .owl-next{
                outline: none;
                width: 45px;
                height: 45px;
                margin: 0;
                background-color: rgb(46, 46, 46);
                transition: .5s;
                &:hover{
                    background-color: #9ec050;
                }
                span{
                    color: #FFF;
                    font-size: 25px;
                    line-height: 1;
                }
            }
        }
        .owl-next{
            position: absolute;
            left:-54px;
        
        }
        .owl-prev{
            position: absolute;
            right: -54px;
        
        }
    
    }
}
