            .bic{

                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
            }
        }
        @media (max-width:991px) {
            display: flex;
        }

        .bic{
            position: relative;
            max-height: 300px;
            img{
                max-width: 100%;
                max-height: auto;
            }
            .overlay{
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(36, 36, 36, 0.8);
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                text-align: center;
                transform: scale(.5) translateY(-100px);
                opacity: 0;
                transition: .5s;
                backface-visibility: hidden;
                h3{
                    font-size: 28px;
                    color: rgb(255, 255, 255);
                    backface-visibility: hidden;
                }
                p{
                    font-size: 14px;
                    color: #fff;
                    backface-visibility: hidden;
    
                }
            }
            &:hover{
                .overlay{
                    transform: scale(1) translateY(0);
                    opacity: 1;
    
                }
            }

        }
        .owl-dots{
            margin-top: 23px;
        }
        .owl-dot{
            display: inline-block;
            outline: none;
        }
        .owl-theme .owl-dots .owl-dot.active span{
            background: #9ec050;
        }
        .owl-theme .owl-dots .owl-dot span{
            background: #777;
    
        }
        .owl-nav{
            display: none;
        }
    }
}
