                            color: transparent;
                        }

                    }
                    &:first-of-type{
                        margin-right: 24px;
                        @media (max-width:1024px) {
                            margin-right: 21px;
                        }
                        
                        @media (max-width:480px) {
                            margin-right: 12.5px;

                        }
                        @media (max-width:375px) {
                            margin-right: 0;
    
                        }
                    }
                }
                textarea{
                    width: 100%;
                    border:1px solid  rgb(52, 52, 52);
                    height: 129px;
                    transition: .5s;
                    padding:19px 23px;
                    background-color: transparent;
                    outline: none;
                    color:rgb(86, 86, 86);
                    margin-bottom: 18px;
                    &::placeholder{
                        color: rgb(52, 52, 52);
                        font-style: italic;
                    }
                    &:focus{
                        border:1px solid  #9ec050;
                        &::placeholder{
                            color: transparent;
                        }

                    }

                }
                button{
                    background-color: rgb(158, 192, 80);
                    width: 118px;
                    height: 40px;
                    border: none;
                    border: 1px solid #9ec050;
                    cursor: pointer;    
                    font-size: 14px;
                    color: rgb(255, 255, 255);
                    font-weight: bold;
                    text-transform: uppercase;
                    transition: .5s;
                    &:hover{
                        background-color: transparent;
                        color: #9ec050;
                        border: 1px solid #9ec050;

                    }
                    &:active{
                        transform: translateY(0);
                        outline: none;
                    }
                }
            }
        }

    }

